import './App.css';
import React from "react";
import {Routes, Route, HashRouter} from 'react-router-dom';
import AdminLayout from './component/admin//index';
import AdminLogin from './component/admin/login';
import AdminRegister from './component/admin/register';
import Main from './component/client/index';
import Modal from "./component/modal";
import {useDispatch, useSelector} from "react-redux";

const App = () =>{

    const modalStatus = useSelector((state) => state);
    const dispatch = useDispatch();

  return (
      <div>
          <HashRouter>
              <Routes>
                  <Route path="/"                   element={<Main />} />
                  <Route path="/admin/*"            element={<AdminLayout />} />
                  <Route path="/admin/login"        element={<AdminLogin />} />
                  <Route path="/admin/register"     element={<AdminRegister />} />
              </Routes>
          </HashRouter>
          <Modal open={modalStatus} close={() => dispatch({type : "CLOSE"})} header=""></Modal>
      </div>
      /* git Page    
      <BrowserRouter basename={process.env.PUBLIC_URL}>
      </BrowserRouter>
      */
  );
}

export default App;
